import { Box, Fade, Grid, Paper, Slide } from "@mui/material"
import React from "react";
import Scroll from "../../class/Scroll";
import Container from "../../components/Container";
import Image from "../../components/Image";
import SubTitle from "../../components/SubTitle"
import aboutusimage from '../../styles/images/aboutus.png'

interface Props { }

const AboutUs = (props: Props) => {
    const [animation, setAnimation] = React.useState<boolean>(false)
    const reference = React.useRef()
    React.useEffect(() => {
        const elem = document.getElementById("aboutus-section")
        Scroll.getVisibleEment(elem, (elementPosition:number, scrollPosition:number) => {
            if(scrollPosition > elementPosition){
                setAnimation(true)
            }else{
                setAnimation(false)
            }
        })
    },[])
    return(
        <Container direction="column" >
            <Box id="aboutus-section" component="span" ref={reference} >
                <Grid container >
                    <Grid item xs={12} >
                        <SubTitle title="QUIENES SOMOS?" activeTrasition={animation} directionTransition="right" />
                    </Grid>
                    <Fade in={animation} style={{ transitionDelay: animation ? '500ms' : '0ms' }} >
                        <Grid item md={6} lg={6} sx={{
                                display:{
                                    xs: "none",
                                    sm: "none",
                                    md: "flex",
                                    lg: "flex",
                                },
                                height:"100%"
                            }} >
                            <Image url={aboutusimage} size="100%" height="auto" />
                        </Grid>
                    </Fade>
                    <Slide direction="down" in={animation} container={reference.current} style={{ transitionDelay: animation ? '500ms' : '0ms' }}>
                        <Grid item xs={12} sm={12} md={6} lg={6} >
                                <Paper elevation={6} sx={{
                                    margin: 3,
                                    borderRadius: 5,
                                    padding: 3
                                }}>
                                        <Box fontWeight="500" sx={{
                                            fontSize:{
                                                lg: "1.19rem",
                                                md: "1.19rem",
                                                sm: "15px",
                                                xs: "15px",
                                            },
                                            textAlign: "left"
                                        }}>
                                            Lorem ipsum dolor sit, amet consectetur adipisicing elit. Natus, illo. Id possimus doloribus, placeat delectus veritatis ea explicabo? Nostrum natus quaerat architecto doloremque aliquam exercitationem, maiores error. Assumenda, quas maiores.
                                            Voluptate, nemo? Accusamus tenetur saepe nesciunt earum illum! Pariatur corporis assumenda accusamus soluta quos reprehenderit iusto quia voluptas, magni inventore debitis aperiam sint ipsam, deserunt temporibus, consectetur unde sit ratione.
                                        </Box>
                                </Paper>
                        </Grid>
                    </Slide>
                </Grid>
            </Box>
        </Container>
    )
}

export default AboutUs