import React from "react"
import { Box, Button, Divider, Fade, Grid, Link, Paper, Slide, Stack, useTheme } from "@mui/material"
import Container from "../../components/Container"
import SubTitle from "../../components/SubTitle"
import TextField from "../../components/TextField"
import Scroll from "../../class/Scroll"
import FacebookRoundedIcon from '@mui/icons-material/FacebookRounded';
import WhatsappRoundedIcon from '@mui/icons-material/WhatsappRounded';

const ContactUs = () => {
    const [animation, setAnimation] = React.useState<boolean>(false)
    const reference = React.useRef()
    const theme = useTheme()
    React.useEffect(() => {
        const elem = document.getElementById("contactus-section")
        Scroll.getVisibleEment(elem, (elementPosition:number, scrollPosition:number) => {
            if(scrollPosition > elementPosition+900){
                setAnimation(true)
            }else{
                setAnimation(false)
            }
        })
    },[])
    return(
        <Container>
            <Box id="contactus-section" component="span" width="100%" >
                <Grid container spacing={2} >
                    <Grid item xs={12} >
                        <SubTitle title="CONTACTANOS" activeTrasition={animation} directionTransition="down" />
                    </Grid>
                    <Grid container >
                        <Grid item xs={6} >
                        <Paper elevation={6} sx={{
                            margin: 3,
                            borderRadius: 5,
                            padding: 3
                        }}>
                            <Box ref={reference} >
                                <Slide in={animation} direction="right" container={reference.current} style={{ transitionDelay: animation ? '500ms' : '0ms' }}>
                                    <div>
                                        <TextField
                                            label="Nombre"
                                        />
                                    </div>
                                </Slide>
                                <Slide in={animation} direction="right" container={reference.current} style={{ transitionDelay: animation ? '700ms' : '0ms' }}>
                                    <div>
                                        <TextField
                                            label="Correo electronico"
                                            type="email"
                                        />
                                    </div>
                                </Slide>
                                <Slide in={animation} direction="right" container={reference.current} style={{ transitionDelay: animation ? '900ms' : '0ms' }}>
                                    <div>
                                        <TextField
                                            label="Asunto"
                                        />
                                    </div>
                                </Slide>
                                <Slide in={animation} direction="right" container={reference.current} style={{ transitionDelay: animation ? '1100ms' : '0ms' }}>
                                    <div>
                                        <TextField
                                            rows={5}
                                            multiline
                                            name="message"
                                            label="Mensaje"
                                        />
                                    </div>
                                </Slide>
                                <Fade in={animation} style={{ transitionDelay: animation ? '1500ms' : '0ms' }}>
                                    <div>
                                        <Button variant="contained" >Enviar</Button>
                                    </div>
                                </Fade>
                            </Box>
                        </Paper>
                        </Grid>
                        <Grid item xs={6} >
                            <Stack direction="row" divider={<Divider orientation="vertical" color={theme.palette.kikedevMain.main} flexItem sx={{ borderRightWidth: "medium" }} />} spacing={2} >
                                <Box display="flex" flexDirection="column" alignItems="center" >
                                    <FacebookRoundedIcon
                                        sx={{
                                            textAlign:"center",
                                            fontSize: "60px",
                                            color: "#3b5998 "
                                        }}
                                    />
                                    <Link href="https://www.facebook.com/kikedevs" target="_blank" >facebook.com/kikedevs</Link>
                                </Box>
                                <Box display="flex" flexDirection="column" alignItems="center" >
                                    <WhatsappRoundedIcon
                                        sx={{
                                            textAlign:"center",
                                            fontSize: "60px",
                                            color: "#25D366 "
                                        }}
                                    />
                                    <Link href="http://wa.me/573107744170" target="_blank" > +57 310 774 41 70 </Link>
                                </Box>
                            </Stack>
                        </Grid>
                    </Grid>
                </Grid> 
            </Box>

        </Container>
    )
}

export default ContactUs